<template>
  <div class="home">
     <h2>{{msg}} , 欢迎来到提瓦特!</h2>
     <button @click="logout">注销</button>
  </div>
</template>

<script>
    import request from "../utils/request"
    export default {
        data() {
            return {
                msg: "",
            }
        },
        methods: {
            logout() {
                localStorage.setItem("token", "");
                location.reload();
            },
            getMsg() {
                request.get(`/hello`).then((res) => {
                    this.msg = res.data.data[0].msg;
                    //res.data是一个对象,有个data属性
                })
            },
        },
        created() {
            this.getMsg();
        }
    }
</script>