<template>
  <div class="login">
    <h1>登录</h1>
    <form @submit.prevent="login">
      <label for="">米忽悠账号: </label>
       <input type="text" v-model="username">
       <br>
       <br>
       <button>进入提瓦特世界</button>
    </form>
  </div>
</template>

<script>
    import request from "../utils/request"
    export default {
        data() {
            return {
                username: '',
            }
        },
        methods: {
            login() {
                console.log(process.env.VUE_APP_TITLE, "环境变量");
                request.post(`/login`, {
                        username: this.username
                    })
                    .then((res) => {
                        if (res.data.code === 20000) {
                            localStorage.setItem("token", res.data.token);
                            this.$router.push("/");
                        } else {
                            console.log('进入提瓦特失败');
                        }
                    })
            },
        },
    }
</script>